  position: relative;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, .06);
}

.img-search {
  width: 25px;
  position: absolute;
  top: 5px;
  left: 8px;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
  flex-direction: column;
  display: flex;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .07), 0 2px 2px rgba(0, 0, 0, .07), 0 4px 4px rgba(0, 0, 0, .07), 0 8px 8px rgba(0, 0, 0, .07), 0 16px 16px rgba(0, 0, 0, .07);
